.modal .modal-dialog button.close span {
    top: -9px;
    right: 0px;
    font-size: 18px;
}
.modal .modal-dialog .modal-title {
    font-weight: 700;
}
.social button, .social a {
    color: #ffffff;
    padding: 6px 28px;
    border-radius: 50px;
    text-transform: uppercase;
    margin: 0 5px;
    font-size: 12px;
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
    border: none;
    width: 48%;
}


