.slogan {
    height: 30px;
}
.slogan .editable-buttons{
    position: absolute;
    right: 0;
    top: 0;
}
.slogan .editable-buttons .btn{
    color: #333 !important;
}
.slogan .form-inline.editableform{
    padding-right: 50px;
}
.slogan .editable.editable-click{
    padding: 0 10px;
    border-bottom: 1px dashed #244b90;
}


