
 * {
 	margin: 0;
 	padding: 0;
 	-webkit-box-sizing: border-box;
 	box-sizing: border-box;
 }

/** ====================
 * Lista de Comentarios
 =======================*/
.comments-container {
	width: 100%;
}

.comments-container h1 {
	font-size: 36px;
	color: #283035;
	font-weight: 400;
}

.comments-container h1 a {
	font-size: 18px;
	font-weight: 700;
}

.comments-list {
	margin-top: 30px;
	position: relative;
}

/**
 * Lineas / Detalles
 -----------------------*/
.comments-list:before {
	content: '';
	width: 2px;
	height: 100%;
	background: #244b90;
	position: absolute;
	left: 32px;
	top: 0;
}

.comments-list:after {
	content: '';
	position: absolute;
	background: #244b90;
	bottom: 0;
	left: 29px;
	width: 7px;
	height: 7px;
	border: 3px solid #244b90;
	border-radius: 50%;
}

.reply-list:before, .reply-list:after {display: none;}
.reply-list li:before {
	content: '';
	width: 60px;
	height: 2px;
	background: #244b90;
	position: absolute;
	top: 25px;
	left: -55px;
}


.comments-list li {
	margin-bottom: 15px;
	display: block;
	position: relative;
}

.comments-list li:after {
	content: '';
	display: block;
	clear: both;
	height: 0;
	width: 0;
}

.reply-list,
.comment-rely {
	padding-left: 88px;
	clear: both;
	margin-top: 15px;
}
.send-button .btn-send{
	background: transparent;
    border: none;
    color: #fff;
}
.send-button .btn-send:focus{
	outline: none;
}
/**
 * Avatar
 ---------------------------*/
.comments-list .comment-avatar {
	width: 65px;
	height: 65px;
	position: relative;
	z-index: 99;
	float: left;
	border: 3px solid #FFF;
	border-radius: 100%;
	-webkit-box-shadow: 0 1px 2px rgba(0,0,0,0.2);
	box-shadow: 0 1px 2px rgba(0,0,0,0.2);
	overflow: hidden;
	background: #fff;
}

.comments-list .comment-avatar img {
	width: 100%;
	height: 100%;
}

.reply-list .comment-avatar {
	width: 50px;
	height: 50px;
}

.comment-main-level:after {
	content: '';
	width: 0;
	height: 0;
	display: block;
	clear: both;
}
/**
 * Caja del Comentario
 ---------------------------*/
.comments-list .comment-box {
	width: calc(100% - 80px);
	float: right;
	position: relative;
	-webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.15);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
}

.comments-list .comment-box:before, .comments-list .comment-box:after {
	content: '';
	height: 0;
	width: 0;
	position: absolute;
	display: block;
	border-width: 10px 12px 10px 0;
	border-style: solid;
	border-color: transparent #FCFCFC;
	top: 8px;
	left: -11px;
}

.comments-list .comment-box:before {
	border-width: 11px 13px 11px 0;
	border-color: transparent rgba(0,0,0,0.05);
	left: -12px;
}

.reply-list .comment-box {
	width: calc(100% - 65px);
}
.comment-box .comment-head {
	background: #FCFCFC;
	padding: 10px 12px;
	border-bottom: 1px solid #E5E5E5;
	overflow: hidden;
	border-radius: 4px 4px 0 0;
}

.comment-box .comment-head i {
	float: right;
	margin-left: 14px;
	position: relative;
	top: 2px;
	color: #A6A6A6;
	cursor: pointer;
	-webkit-transition: color 0.3s ease;
	-o-transition: color 0.3s ease;
	transition: color 0.3s ease;
}

.comment-box .comment-head i:hover {
	color: #03658c;
}

.comment-box .comment-name {
	color: #283035;
	font-size: 14px;
	font-weight: 700;
	float: left;
	margin-right: 10px;
}

.comment-box .comment-name a {
	color: #283035;
}

.comment-box .comment-head span {
	float: left;
	font-size: 13px;
	position: relative;
	top: 1px;
}
.status{
	margin-left: 10px;
}

.comment-box .comment-content {
	background: #FFF;
	padding: 12px;
	font-size: 15px;
	color: #595959;
	border-radius: 0 0 4px 4px;
}

.comment-box .comment-name.by-author, .comment-box .comment-name.by-author a {color: #03658c;}
.comment-box .comment-name.by-author:after {
	content: 'autor';
	background: #03658c;
	color: #FFF;
	font-size: 12px;
	padding: 3px 5px;
	font-weight: 700;
	margin-left: 10px;
	border-radius: 3px;
}
.send-button{
	width: 45px;
    height: 45px;
    background: #244b90;
    line-height: 45px;
    text-align: center;
    border-radius: 50px;
    position: absolute;
    top: 4px;
    right: 5px;
}
.send-button a.btn-send{
	color: #fff;
}
.comment-rely .comment-avatar{
	width: 45px;
	height: 45px;
	position: absolute;
    top: 3px;
    left: 4px;
    border: none;
}
.comment-rely .comment-form{
	position: relative;
}
.comment-rely textarea.form-control{
	padding: 6px 60px;
}
.wrap-area{
	border-radius: 50px;
	padding: 0px 60px;
	border: 1px solid #e3e3e3;
}
.wrap-area textarea{
	border-radius: 0px;
	padding: 5px 15px !important;
	border: none;
}
/** =====================
 * Responsive
 ========================*/
@media only screen and (max-width: 766px) {
	
}


