.report {
    position: fixed;
    bottom: 30px;
    right: 100px;
    z-index: 99;
    padding: 5px;
    background: white;
    padding-right: 10px;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 10px 0px rgb(108, 161, 255);
            box-shadow: 0px 0px 10px 0px rgb(108, 161, 255);
    border-radius: 50px;
}
.report img {
    width: 35px;
    border-radius: 50px;
}
.report * {
    color: #000;
    text-decoration: none;
    background: transparent;
    outline: none !important;
}
.modal-title img{
    width: 50px;
}
.modal .modal-dialog .modal-content{
    margin-top: 25%;
    border-radius: 0px;
}
.modal .modal-dialog .form-control,
.modal .modal-dialog button{
    -webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.15);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
}
.btn-group button{
    border-radius: 50px
}
.feedback.box-upload{
    height: auto;
    padding: 0 10px;;
}
.modal .modal-dialog .feedback  button.close span{
    top: -23px;
    right: 7px;
}


