.user-detail .icon-block.level{
    left: 15px;
    top: 5px;
}
.icon-block.level ul li{
    position: relative;
    padding-left: 51px;
    padding-right: 20px !important;
    line-height: 51px;
}
.icon-block.level ul li img{
    position: absolute;
    top: 0;
    left: 0;
}
.icon-block.level .editable-buttons{
    top: -17px;
    right: 0px;
}
.icon-block.level .editable,
.icon-block.level span {
    white-space: nowrap;
}
.icon-block.level .editable-container .input-sm{
    padding-right: 40px;
}


